body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html {
  font-size: 62.5%;
}

.calculator {
  width: 100%;
  height: 100%;
  font-size: 3rem;
  display: flex;
  flex-direction: column;
}

.calculator .noti {
  position: absolute;
  color: red;
  background-color: blue;
}

.calculator-contain {
  margin: 0 auto;
  max-width: 65rem;
  height: 100vh;
  position: relative;
}

.calculator-screen {
  background-color: #2d3838;
  color: white;
  border: 2px solid black;
  border-bottom: none;
  min-height: 20rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.calculator-screen > * {
  height: 50%;
  padding: 1rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  /* Handle */
  /* Handle on hover */
}

.calculator-screen > * ::-webkit-scrollbar {
  height: 5px;
}

.calculator-screen > * ::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.calculator-screen > * ::-webkit-scrollbar-thumb {
  background-color: #ffa1ff;
}

.calculator-screen > * ::-webkit-scrollbar-thumb:hover {
  background-color: #ff87ff;
}

.calculator-screen > * > span {
  overflow-x: auto;
}

.calculator-keyboard {
  flex-grow: 1;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: (1fr)[5];
      grid-template-rows: repeat(5, 1fr);
}

.calculator-keyboard__row {
  border: solid black;
  border-width: 1px 2px 0 2px;
  display: -ms-grid;
  display: grid;
}

.calculator-keyboard__row:last-child {
  border-bottom-width: 2px;
}

.calculator-keyboard__row:first-child {
  -ms-grid-columns: 1fr 1fr 25%;
      grid-template-columns: 1fr 1fr 25%;
}

.calculator-keyboard__row:not(:first-child):not(:last-child) {
  -ms-grid-columns: (25%)[4];
      grid-template-columns: repeat(4, 25%);
}

.calculator-keyboard__row:last-child {
  -ms-grid-columns: 25% 25% 1fr;
      grid-template-columns: 25% 25% 1fr;
}

.calculator-keyboard__button {
  border: 1px solid black;
  border-top: none;
  border-bottom: none;
  font-size: inherit;
  color: white;
  outline: none;
  padding: 1.5rem;
  transition: 0.2s;
}

.calculator-keyboard__button:first-child {
  border-left-width: 0;
}

.calculator-keyboard__button:last-child {
  border-right-width: 0;
}

.calculator-keyboard__button--num {
  background-color: #169e9e;
}

.calculator-keyboard__button--deleteAll {
  background-color: #bd0808;
}

.calculator-keyboard__button--delete {
  background-color: #c26208;
}

.calculator-keyboard__button--math {
  color: black;
  background-color: #f5e900;
}

.calculator-keyboard__button--dot {
  color: black;
}

.calculator-keyboard__button--submit {
  background: #00d12d;
}

.calculator-keyboard__button:hover {
  transform: scale(1.1);
  border: 1px solid white;
}

@media screen and (max-width: 480px) {
  .calculator-screen {
    min-height: auto;
  }
}
